@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin LargeScreens {
  @media (min-width: 2560px) {
    @content;
  }
}

@mixin FullHD {
  @media (min-width: 1600px) {
    @content;
  }
}

@mixin Notebook {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin Tablet {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin Mobile {
  @media (min-width: 300px) {
    @content;
  }
}
